footer {
    background: rgb(73, 79, 82);
    /* position: fixed; */
    bottom: 0;
    width: 100%;
    /* margin: 20px 0; */
    padding: 10px;
    color: white;
  }
  
  .ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    top: 0;
    z-index: 1;
    list-style-type: none;
  }

  .Icon {
    height: 50px;
    width: 50px;
    transition: 1500ms;
  }
  .Icon:hover {
    border-radius: 50%;
    border: 1px solid aliceblue;
    background-color: aliceblue;
    transition: 1500ms;
    height: 150%;
  }
  .circle {
    border-radius: 50%;
  } 

  .name {
    color: aliceblue;
    -webkit-text-stroke: 0.2px black;
    transition: 1500ms;
  }
  .name:hover {
    color: black;
    transition: 1500ms;
  }